.search-form {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card .info {
  background-color: #f2f2f2;
  padding: 10px;
}

.photo-card .info-item {
  margin: 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #428bca;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

